.aboutMe {
    height: 100vh;
    display: flex;
    align-items: center;
    background: url('../../img/Intro-bg.jpg') no-repeat center center fixed;
    background-size: cover;
    margin-bottom: 50px;
}

.aboutMe-left {
    flex: 20%;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    background-color: rgb(125, 249, 255, .9);
    height: 81.2%;
}

.aboutMe-right {
    flex: 80%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(0, 0, 0, .9);
    border-top:#DFFF00 5px solid;
    border-bottom:#DFFF00 5px solid;
    /* display: flex; */
    flex-direction: column;
    height: 80%
    /* justify-content: center; */
}


.aboutMe-card.bg {
    position: absolute;
    top: 50px;
    left: 50px;
    /* background-color: rgb(125,249,255); */
}

.aboutMe-card {
    width: 60%;
    height: 70vh;
    border-radius: 20px;
    position: relative;
}

.aboutMe-img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    /* border-radius: 20px; */
}

.aboutMe-title {
    font-size: 65px;
    font-family: 'Roboto';
    color: white;
    font-weight: bold;


}

.aboutMe-text {
    font-size: 25px;
    font-family: 'Playfair', sans-serif;
    color: white;
    margin-top: 35px;
}

.aboutMe-right-wrapper {
    display: flex;
    flex-direction: column;
    padding: 80px;
    height: 65vh;
    width: 75%;
    /* background-color: rgba(0, 0, 0, .85); */
    
    border-radius: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-left: 20px;

}