    font-family: 'Poppins', sans-serif;
} */
.intro {
    display: flex;
    height: 750px;
    background: url('../../img/Intro-bg.jpg') no-repeat center center fixed;
    background-size: cover;
    /* margin-bottom: 50px; */
    
}

.intro-left {
    flex: 50%;
    display: flex;
    align-items: center;
    /* justify-content: center; */
}

.intro-left-wrapper {
    padding: 80px;
    height: 50%;
    width: 100%;
    background-color: rgb(0, 0, 0, .8);
    /* opacity: .9; */
    /* border: #DFFF00 solid 4px; */
    /* border-radius: 20px; */
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin-left: 20px;
   border: solid 5px #DFFF00
}

.intro-right {
    flex: 50%;
    position: relative;
}

.intro-hi {
    font-size: 25px;
    /* font-weight: bold; */
    font-family: 'Playfair', sans-serif;
}

.intro-name {
    font-size: 90px;
    font-family: 'Roboto', sans-serif;
    color: white;

}

.intro-role {
    font-size: 60px;
    font-family: 'Playfair', sans-serif;
    color: rgb(125, 249, 255);
}

.intro-skills {
    height: 60px;
    overflow: hidden;

}

.intro-skills-wrapper {
    height: 100%;
    animation: move 10s ease-in-out infinite alternate;
}

@keyframes move {
    25% {
        transform: translateY(-60px);
    }

    50% {
        transform: translateY(-120px);
    }

    75% {
        transform: translateY(-180px);
    }

    100% {
        transform: translateY(-240px);
    }

}

.intro-skills-item {
    height: 60px;
    width: 250px;
    font-size: 35px;
    font-weight: bold;
    /* color: #00A4B4; */
    display: flex;

    font-family: 'Merriweather';

    color: #FF10F0;


}

.my-pic {
    height: 100%;
    position: absolute;
    /* margin-top: 30px; */
    margin-left: 300px;
    /* border-radius: 20px; */
}

.image-bg {
    clip-path: polygon(100% 0%, 100% 57%, 100% 100%, 25% 100%, 0% 50%, 25% 0%);
    background-color: rgb(125, 249, 255, .85);
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    right: 0;

}