.sub-section {
    display: flex;
    justify-content: space-around;
    padding: 80px;
    font-family: 'Roboto', sans-serif;
    /* border-top: solid #181A18 3px; */
    /* border-bottom: solid #181A18 3px; */

}

.me {
    width: 330px;
    border: solid 4px #d9e506;
    text-align: center;
    margin-top: 50px;
} 

.my-projects-title {
    font-size: 60px;
    font-family: 'Roboto';
    color: black;
    font-weight: bold;
    padding-bottom: 40px;
}

.project-card {
    width: 400px;
    height: 600px;
    border: #181A18 solid 3px;
    border-radius: 15px;
    background-color: white;

}

.project-container {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    
}

.project-with-title-container {
    background-color: rgb(125, 249, 255, .9);
    padding-top: 50px;
    padding-bottom: 50px;
}

.sub-section-2 {
    display: flex;
    flex-direction: column;
    text-align: center;
    padding: 80px;
    background: url('../../img/Intro-bg.jpg') no-repeat center center fixed;
    background-size: cover;
}



.project-image {
    width: 400px;
    border-radius: 10px;
}

